import { Component, EventEmitter, Output } from '@angular/core';
import { NodeDetailAbstract } from '../../../../../core/classes/node-detail.abstract';

@Component({
  selector: 'app-input',
  templateUrl: './input.component.html',
  styleUrl: './input.component.scss'
})
export class InputDetailComponent extends NodeDetailAbstract {

  public type!: string;
  public index!: number;

  @Output() received = new EventEmitter<any>();

  public override dataInit(): void {
    this.type = this.data.type;
    this.currentData = this.data.node;
    this.index = this.data.index;
  }

}