import { Component, ComponentRef, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { StoreAbstract } from '../../../../core/classes/store.abstract';

@Component({
  selector: 'app-chatflow-detail',
  templateUrl: './chatflow-detail.component.html',
  styleUrl: './chatflow-detail.component.scss'
})
export class ChatflowDetailComponent extends StoreAbstract implements OnInit {

  @ViewChild('container', { read: ViewContainerRef, static: true }) 
  public container!: ViewContainerRef;

  private component!: ComponentRef<any>;
  private type!: string;

  ngOnInit() {
    this.logicService.nodeDetailContainer = this.container;
  }

}
