import { Component, Input } from '@angular/core';
import { NodeType } from '../../interfaces/chatflow.dto';
import { StoreAbstract } from '../../../../core/classes/store.abstract';
import { INode } from '../../interfaces/chatflow.interface';

@Component({
  selector: 'app-chatflow-header',
  templateUrl: './chatflow-header.component.html',
  styleUrl: './chatflow-header.component.scss'
})
export class ChatflowHeaderComponent extends StoreAbstract {

  @Input() tabIndex!: number;

  public speakType: NodeType = "speak";
  public slotType: NodeType = "slot";
  public behaviorType: NodeType = "behavior";
  public inputType: NodeType = 'input';
  public chatBoxType: NodeType = 'chatBox';
  public buttonType: NodeType = 'button';

  /**
   * 노드 추가 함수
   *  - 생성의 경우, id를 그냥 가져가면 됨
   *  - 수정의 경우, id를 현재 노드의 id보다 하나 크게
   */
  public createNode(event: MouseEvent, nodeType: NodeType) { 
    const chatflow = this.logicService.getChatflow(this.logicService.selectedLogic);
    let index: number = 0;
    if (chatflow) index = this.logicService.getAvailableIndex(chatflow);

    const node = {
      id: this.logicService.getGUID(),
      type: nodeType,
      node: {
        category: nodeType,
        result: [],
        isAwait: false
      },
      index: index,
      left: false,
      right: false
    };

    this.addNode(node);
  }

  public editNode(event: MouseEvent, nodeType: NodeType) {
    const chatflow = this.logicService.getChatflow(this.logicService.selectedLogic);
    const index = this.logicService.getAvailableIndex(chatflow);

    const node = {
      id: this.logicService.getGUID(),
      type: nodeType,
      node: {
        category: nodeType,
        result: [],
        isAwait: false
      },
      index: index,
      left: false,
      right: false
    };

    this.addNode(node);
  }

  public onNameChange(event: string) {
    this.logicService.selectedLogic = event;
  }

  public onChatflowChange(event: string) {
    this.logicService.selectedLogic = event;
    
    this.logicService.chatflowContainer[this.logicService.currentTabIndex].clear();
    const chatflow = this.logicService.getLogicValues(event);
    if (!chatflow) return;
    chatflow.forEach((node: INode, index: number) => {
      this.addNode(node);
    })
  }

  public saveLogic(event: MouseEvent) {
    if (!this.logicService.selectedLogic) return;
    this.logicService.logics.push(this.logicService.selectedLogic);
    this.logicService.setLogic(this.logicService.selectedLogic);
    
    this.logicService.chatflowContainer[this.logicService.currentTabIndex].clear();
    this.logicService.selectedLogic = '';
  }

  public updateLogic(event: MouseEvent) {
    if (!this.logicService.selectedLogic) return;
    this.logicService.setLogic(this.logicService.selectedLogic);
  }

}
