import { ElementRef } from "@angular/core";
import { INodeData } from "../../pages/chatflow/interfaces/chatflow.interface";
import { INodeDetail } from "../interfaces/node-detail.interface";
import { DataAbstract } from "./data.abstract";

export abstract class NodeDetailAbstract extends DataAbstract implements INodeDetail {

  public inputElement!: ElementRef<HTMLInputElement>;
  public currentData!: INodeData;
  
  addResult(choice: string): void {
    if (choice) {
      this.currentData.result = [...this.currentData.result, choice];

      this.logicService.setChatflow(this.logicService.selectedLogic, {
        id: this.data.id,
        node: {
          category: this.data.node.category,
          result: this.currentData.result,
          isAwait: this.data.node.isAwait
        }
      });

      this.deliver('save', {
        id: this.data.id,
        node: {
          category: this.data.node.category,
          result: this.currentData.result,
          isAwait: this.data.node.isAwait
        }
      });
    }
    this.inputElement.nativeElement.value = '';
  }

  removeResult(index: number): void {
    if (index >= 0) {
      this.currentData.result = this.currentData.result.filter((_, i) => i !== index);

      this.logicService.setChatflow(this.logicService.selectedLogic, {
        id: this.data.id,
        node: {
          category: this.data.node.category,
          result: this.currentData.result,
          isAwait: this.data.node.isAwait
        }
      });

      this.deliver('save', {
        id: this.data.id,
        node: {
          category: this.data.node.category,
          result: this.currentData.result,
          isAwait: this.data.node.isAwait
        }
      });
    }
  }

  onNodeCategoryChange(event: any): void {
    this.currentData.category = event;
    this.logicService.setChatflow(this.logicService.selectedLogic, {
      id: this.data.id,
      node: {
        category: event,
        result: this.data.node.result,
        isAwait: this.data.node.isAwait
      }
    });

    this.deliver('save', {
      id: this.data.id,
      node: {
        category: event,
        result: this.data.node.result,
        isAwait: this.data.node.isAwait
      }
    });
  }

  onResultChange(event: any): void {
    this.logicService.setChatflow(this.logicService.selectedLogic, {
      id: this.data.id,
      node: {
        category: this.data.node.category,
        result: [event],
        isAwait: this.data.node.isAwait
      }
    });

    this.deliver('save', {
      id: this.data.id,
      node: {
        category: this.data.node.category,
        result: [event],
        isAwait: this.data.node.isAwait
      }
    });
  }

  onIsAwaitChange(event: any): void {
    this.currentData.isAwait = event;
    this.logicService.setChatflow(this.logicService.selectedLogic, {
      id: this.data.id,
      node: {
        category: this.data.node.category,
        result: this.data.node.result,
        isAwait: event
      }
    });

    this.deliver('save', {
      id: this.data.id,
      node: {
        category: this.data.node.category,
        result: this.data.node.result,
        isAwait: event
      }
    });
  }

  onIndexChange(event: any): void {
    this.logicService.setChatflow(this.logicService.selectedLogic, {
      id: this.data.id,
      index: event
    });

    this.deliver('save', {
      id: this.data.id,
      index: event
    });
  }

}