import { Component } from '@angular/core';
import { NodeDetailAbstract } from '../../../../../core/classes/node-detail.abstract';

@Component({
  selector: 'app-select',
  templateUrl: './select.component.html',
  styleUrl: './select.component.scss'
})
export class SelectComponent extends NodeDetailAbstract {

  public type!: string;
  public index!: number;

  public behaviors = ['concat', 'prompt', 'llm', 'category', 'embedding'];

  public override dataInit(): void {
    this.type = this.data.type;
    this.currentData = this.data.node;
    this.index = this.data.index;
  }

  public onSelectChange(event: any) {
    this.onIsAwaitChange(true);
    this.onNodeCategoryChange(event);

    switch (event) {
      case 'llm':
        this.onResultChange('문서 조회 중');
        break;
      case 'category':
        this.onResultChange('카테고리를 선택해 주세요.');
        break;
      case 'embedding':
        this.onResultChange('문서 학습 중');
        break;
      default:
        this.onIsAwaitChange(false);
    }
  }
  
}
