import { ContentChild, Directive, ElementRef, EventEmitter, HostListener, Input, Output } from '@angular/core';
import { GlobalService } from '../../../core/services/global.service';
import { SpotComponent } from '../components/spot/spot.component';

@Directive({
  selector: '[appNode]'
})
export class NodeDirective {
  
  @ContentChild(SpotComponent) leftSpot!: SpotComponent;
  @ContentChild(SpotComponent) right!: SpotComponent;

  @Output() moveCoordinateEmitter = new EventEmitter<any>();

  private boundDrag = this.drag.bind(this);
  private boundDragEnd = this.dragEnd.bind(this);

  constructor(
    private el: ElementRef, 
    private globalService: GlobalService
  ) {}

  @HostListener('mouseenter', ['$event'])
  public mouseEnter() {
    this.globalService.layoutService.isShow = true;
  }

  @HostListener('mouseleave', ['$event'])
  public mouseLeave() {
    this.globalService.layoutService.isShow = false;
  }

  @HostListener('mousedown', ['$event'])
  public dragStart(event: MouseEvent) {
    event.preventDefault();
    if (this.globalService.layoutService.isDragDisable) return;
    document.addEventListener('mousemove', this.boundDrag);
    document.addEventListener('mouseup', this.boundDragEnd);
  }

  public drag(event: MouseEvent) {
    // left, right spot의 좌표 값이 계속 찍혀야함 (action up)
    this.moveCoordinateEmitter.emit({
      left: {
        x: this.el.nativeElement.children[0].getBoundingClientRect().x, 
        y: this.el.nativeElement.children[0].getBoundingClientRect().y,
      },
      right: {
        x: this.el.nativeElement.children[2].getBoundingClientRect().x, 
        y: this.el.nativeElement.children[2].getBoundingClientRect().y
      }
    });
  }

  public dragEnd(event: MouseEvent) {
    document.removeEventListener('mousemove', this.boundDrag);
    document.removeEventListener('mouseup', this.boundDragEnd);
  }

}
