import { Component, ElementRef, EventEmitter, Output, ViewChild } from '@angular/core';
import { NodeDetailAbstract } from '../../../../../core/classes/node-detail.abstract';

@Component({
  selector: 'app-list-box',
  templateUrl: './list-box.component.html',
  styleUrl: './list-box.component.scss'
})
export class ListBoxComponent extends NodeDetailAbstract {

  @ViewChild('choiceInput', { read: ElementRef, static: true }) choiceInput!: ElementRef<HTMLInputElement>;

  public type!: string;
  public index!: number;

  @Output() received = new EventEmitter<any>();

  public override dataInit(): void {
    this.type = this.data.type;
    this.currentData = this.data.node;
    this.index = this.data.index;

    this.inputElement = this.choiceInput;
  }

}
