import { Component, ComponentRef, ElementRef, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild, ViewContainerRef } from '@angular/core';
import { StoreAbstract } from '../../../../core/classes/store.abstract';
import { NodeComponent } from '../../components/node/node.component';
import * as d3 from 'd3';
import { Subscription, concatMap, filter, finalize, switchMap, take, tap } from 'rxjs';

export interface INodeEvent {
  tabIndex: number,
  container: ViewContainerRef
}
@Component({
  selector: 'app-chatflow-zoom',
  templateUrl: './chatflow-zoom.component.html',
  styleUrl: './chatflow-zoom.component.scss'
})
export class ChatflowZoomComponent extends StoreAbstract implements OnInit, OnDestroy {

  @ViewChild('canvas', { static: true }) 
  public canvas!: ElementRef<SVGElement>;
  @ViewChild('container', { read: ViewContainerRef, static: true }) 
  public container!: ViewContainerRef;

  @Input() tabIndex!: number;
  @Output() sendContainer = new EventEmitter<INodeEvent>();

  private subscribtion!: Subscription;
  private component!: ComponentRef<NodeComponent>;
  private x1!: number;
  private y1!: number;
  private x2!: number;
  private y2!: number;

  ngOnInit() {
    this.logicService.chatflow[this.tabIndex] = this.container;
    
    this.sendContainer.emit({
      tabIndex: this.tabIndex,
      container:  this.container
    });
    
    this.subscribtion = this.node$.pipe(
      filter(d => !!d.type && this.logicService.currentTabIndex === this.tabIndex),
    ).subscribe(d => {
      console.log('this.node$^^', d);
      this.component = this.factoryService.createComponent(
        NodeComponent,
        this.logicService.chatflowContainer[this.tabIndex],
        d
      );
      this.logicService.setChatflow(this.logicService.selectedLogic, d);
      this.logicService.setChatflowComponentRef(this.logicService.selectedLogic, {
        id: d.id,
        componentRef: this.component
      });
  
      const destroySubscription = this.component.instance.componentEmit
        .subscribe(d => {
          // 동적 컴포넌트 지우기
          this.factoryService.destroyComponent(d.componentRef);
          // 저장했던 componentRef 정보 지우기 
          this.logicService.chatflowComponentRefs[this.logicService.selectedLogic]
            = this.logicService.getChatflowComponentRefLogic(
              this.logicService.selectedLogic
            ).filter((node: any) => node.id !== d.id);
          // 클라이언트 service 데이터 지우기 
          this.logicService.chatflow[this.logicService.selectedLogic]
            = this.logicService.getChatflow(
              this.logicService.selectedLogic
            ).filter((node: any) => node.id !== d.id);
          // DB에서 지우기
          this.logicService.setLogic(this.logicService.selectedLogic);
          destroySubscription.unsubscribe();
        });
    });
  }

  ngOnDestroy() {
    console.log('zoomComponent ngOnDestroy', this.subscribtion);
    if (this.subscribtion) this.subscribtion.unsubscribe();
    this.clearNode();
  }

}
