import { Component, ComponentRef, EventEmitter, OnDestroy, OnInit, Output, Type } from '@angular/core';
import { INode, INodeData } from '../../interfaces/chatflow.interface';
import { InputDetailComponent } from '../details/input/input.component';
import { ListBoxComponent } from '../details/list-box/list-box.component';
import { SelectComponent } from '../details/select/select.component';
import { Subscription } from 'rxjs';
import { DataAbstract } from '../../../../core/classes/data.abstract';

export const DetailComponentType: {[key: string]: Type<any>} = {
  "speak": InputDetailComponent,
  "slot": ListBoxComponent,
  "behavior": SelectComponent,
}

@Component({
  selector: 'app-node',
  templateUrl: './node.component.html',
  styleUrl: './node.component.scss'
})
export class NodeComponent extends DataAbstract implements OnInit, OnDestroy {

  public id!: string;
  public type!: string;
  public node!: INodeData;
  public index!: number;
  public left: boolean = false;
  public right: boolean = false;

  private subscription!: Subscription;

  @Output() componentEmit = new EventEmitter<{
    id: string;
    componentRef: ComponentRef<NodeComponent>
  }>();
  // @Output() coordinateEmitter = new EventEmitter<any>();
  //@Output() dragNodeEmitter = new EventEmitter<any>();

  public override dataInit(): void {
    this.id = this.data.id
    this.type = this.data.type;
    this.node = this.data.node;
    this.index = this.data.index;
    this.left = this.data.left;
    this.right = this.data.right;
  }

  ngOnInit(): void {    
    this.subscription = this.deliver$.subscribe(d => {
      //if (d.type !== "save") return;
      if (d.data.id !== this.id) return;

      if (d.data.node) this.node = d.data.node;
      if (d.data.index) this.index = d.data.index;
      if (d.data.left) this.left = d.data.left;
      if (d.data.right) this.right = d.data.right;
    });
  }

  public clickDetail() {
    this.logicService.nodeDetailDrawer.toggle();
    
    const data =  {
      id: this.id,
      type: this.type,
      node: {
        category: this.node.category,
        result: this.node.result,
        isAwait: this.node.isAwait
      },
      index: this.index,
      left: this.left,
      right: this.right
    };

    this.logicService.nodeDetailContainer.clear();
    this.factoryService.createComponent(
      DetailComponentType[this.type],
      this.logicService.nodeDetailContainer,
      data
    );
  }

  public clickDelete(event: MouseEvent) {
    const data = this.logicService.getChatflowComponentRef(this.logicService.selectedLogic, this.id);
    this.componentEmit.emit({
      id: data.id,
      componentRef: data.componentRef
    });
  }

  public stringify(obj: any): string {
    return JSON.stringify(obj, null, 2);
  }

  ngOnDestroy() {
    console.log('nodeComponent ngOnDestroy');
    if (this.subscription) this.subscription.unsubscribe();
  }

}
