import { Component, Input, ViewChild, inject } from '@angular/core';
import { DataAbstract } from '../../../../../core/classes/data.abstract';
import { Router, ActivatedRoute } from '@angular/router';
import { ChatHistoryMenuData } from '../../../../chatbot/components/classes/chat-history.menu';
import { MenuComponent } from '../../../../../components/menu-item/menu/menu.component';
import { IChatHistoryRequest } from '../../../../../core/http/chat-history.http';

@Component({
  selector: 'app-list-item',
  templateUrl: './list-item.component.html',
  styleUrl: './list-item.component.scss'
})
export class ListItemComponent extends DataAbstract {

  @ViewChild('menu', { static: false }) menuComponent!: MenuComponent;

  @Input() id: string = '';
  @Input() message: string = '';
  @Input() type: string = '';

  public isChecked: boolean = false;
  public isClicked: boolean = false;

  public historyDeleteData: IChatHistoryRequest = {
    userIdx: this.currentUser.userData.userIdx,
    chatPageId: ''
  };

  private readonly router = inject(Router);
  private readonly route = inject(ActivatedRoute);
  
  public historyMenu = new ChatHistoryMenuData();
  
  public onClickListItem(pageId: string) {
    this.historyService.currentChatPageId = pageId;

    this.historyService.allhistoryList = null;
    this.historyService.pageHistoryList = null;
    this.historyService.historyMetadata = [];

    this.dalService.chatHistoryHttp.get(this.currentUser.userData.userIdx).subscribe(response => {
      this.historyService.allhistoryList = JSON.parse(response.data);
      this.historyService.pageHistoryList = this.historyService.allhistoryList.find((page: any) => page.key === this.historyService.currentChatPageId);

      this.router.navigateByUrl(`/chatbot/${pageId}`);
      this.globalService.chatflow.scenario('history');
    });
  }

  public onClickRight(event: MouseEvent, id: string) {
    event.preventDefault();
    this.historyDeleteData = {
      userIdx: this.currentUser.userData.userIdx,
      chatPageId: id
    };
    this.menuComponent.openMenu();
  }

  // TODO: DOM에서 호출하기 떄문에 개선 필요 (중요이슈)
  public getClicked(id: string) {
    return id === (this.route.params as any).value.chatPageId;
  }

}
