import { Component, OnDestroy, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { StoreAbstract } from '../../core/classes/store.abstract';
import { MatDrawer } from '@angular/material/sidenav';
import { INodeEvent } from './page/chatflow-zoom/chatflow-zoom.component';

// export const DataType: {[key: string]: NodeData} = {
//   "speak": speakData,
//   "slot": slotData,
//   "behavior": behaviorData,
// }

@Component({
  selector: 'app-chatflow',
  templateUrl: './chatflow.component.html',
  styleUrl: './chatflow.component.scss'
})
export class ChatflowComponent extends StoreAbstract implements OnInit, OnDestroy {

  @ViewChild('detailDrawer', { static: true }) 
  public detailDrawer!: MatDrawer;

  ngOnInit() {
    super.OnResizeRedux();
    this.logicService.nodeDetailDrawer = this.detailDrawer;
  }

  public onTabIndexChange(event: number) {
    this.logicService.currentTabIndex = event;
    this.logicService.chatflowContainer.forEach((container: ViewContainerRef) => {
      // console.log('onTabIndexChange!!!!!!!!');
      container.clear();
    });
    this.logicService.selectedLogic = '';
  }

  public receivedContainer(event: INodeEvent) {
    this.logicService.chatflowContainer[event.tabIndex] = event.container;
  }

  ngOnDestroy() {
    console.log('ChatflowComponent ngOnDestroy');
    this.logicService.selectedLogic = '';
    this.logicService.chatflowComponentRefs = {
      "main": [],
    };
  }

}
